r las ideas o practicarlas físicamente ayuda a consolidarlas. Este tipo de memoria es fundamental para el aprendizaje de habilidades prácticas, como deportes, música, pintura o cualquier experiencia ligada al movimiento, el tacto o el cuerpo.")])])]),_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-5 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/17.png")}})])]),_c('div',{staticClass:"col-lg-7"},[_c('h5',[_vm._v("Memoria reproductiva o mecánica")]),_c('p',[_vm._v("También conocida como “aprender de memoria”. Consiste en repetir exactamente frases u oraciones aprendidas. Se basa en la repetición sin necesidad de comprensión. Es útil para aprender: tablas aritméticas, ortografía, definiciones, leyes o autores. Sin embargo, si se usa de forma exclusiva, genera un aprendizaje frágil y poco significativo.")])])]),_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-5 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/18.png")}})])]),_c('div',{staticClass:"col-lg-7"},[_c('h5',[_vm._v("Memoria lógica o comprensiva")]),_c('p',[_vm._v("Implica comprensión y análisis del material. El estudiante no solo recuerda, sino que entiende lo que aprende, reconociendo características importantes y su conexión con otros conceptos. Está vinculada al razonamiento, a la organización de la información y a la integración del nuevo conocimiento con conocimientos previos. Es más duradera y significativa.")])])])])],1)]),_c('p',{staticClass:"mb-5"},[_vm._v("Los psicólogos cognitivos como Atkinson, Shiffrin, Norman y Hunt propusieron que existen dos mecanismos de memoria cualitativamente distintos: algunos sujetos pueden recordar gran cantidad de información de forma inmediata pero la olvidan al poco tiempo (memoria a corto plazo); otros presentan mayor dificultad inmediata, pero conservan la información por largo tiempo (memoria a largo plazo). Por ejem